import "./styles.css";
import Display from "./Display";
import Button from "./Button";
import React from "react";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      count: 5
    };
  }
  updateState = (valueToAdd) => {
    this.setState((prevState) => ({
      count: prevState.count + valueToAdd
    }));
  };
  render() {
    return (
      <div>
        <Display data={this.state.count} />
        <Button incrementValue={5} clickHandler={this.updateState} />
        <Button incrementValue={10} clickHandler={this.updateState} />
        <Button incrementValue={20} clickHandler={this.updateState} />
      </div>
    );
  }
}
